              NEWCPL = Number((Number(data.bidprice-this.holdPositions[i].average)*this.holdPositions[i].volume*this.holdPositions[i].priceperpoint*this.holdPositions[i].rate).toFixed(this.holdPositions[i].digits))*-1;
            }else {
              NEWPL = Number((Number(data.price-this.holdPositions[i].average)*this.holdPositions[i].volume*this.holdPositions[i].priceperpoint).toFixed(this.holdPositions[i].digits))*-1;
              NEWCPL = Number((Number(data.price-this.holdPositions[i].average)*this.holdPositions[i].volume*this.holdPositions[i].priceperpoint*this.holdPositions[i].rate).toFixed(this.holdPositions[i].digits))*-1;
            }
          }else {
            if(this.holdPositions[i].type === 4){
              NEWPL = Number((Number(data.askprice-this.holdPositions[i].average)*this.holdPositions[i].volume*this.holdPositions[i].priceperpoint).toFixed(this.holdPositions[i].digits));
              NEWCPL = Number((Number(data.askprice-this.holdPositions[i].average)*this.holdPositions[i].volume*this.holdPositions[i].priceperpoint*this.holdPositions[i].rate).toFixed(this.holdPositions[i].digits));
            }else {
              NEWPL = Number((Number(data.price-this.holdPositions[i].average)*this.holdPositions[i].volume*this.holdPositions[i].priceperpoint).toFixed(this.holdPositions[i].digits));
              NEWCPL = Number((Number(data.price-this.holdPositions[i].average)*this.holdPositions[i].volume*this.holdPositions[i].priceperpoint*this.holdPositions[i].rate).toFixed(this.holdPositions[i].digits));
            }
          }
          // console.log(newVal.price,this.holdPositions[i].average,this.holdPositions[i].priceperpoint,this.holdPositions[i].volume)
          this.$set(this.holdPositions[i],'NEWPRICE',NEWPRICE);
          this.$set(this.holdPositions[i],'NEWPL',NEWPL);
          this.$set(this.holdPositions[i],'NEWCPL',NEWCPL);
        }
      }
      for(let i=0,len = this.entrustList.length;i<len;i++) {
        let data = this.tickCopyData[this.entrustList[i].instrumentid]
        if (data !== undefined) {
          this.$set(this.entrustList[i], 'bidPrice', (data.bidprice).toFixed(this.entrustList[i].digits));
          this.$set(this.entrustList[i], 'askPrice', (data.askprice).toFixed(this.entrustList[i].digits));
        }
      }
      this.PLList = this.holdPositions.reduce((totalPrice, item) =>totalPrice + item.NEWCPL , 0)
      this.allBond =
      this.occupyBond = this.holdPositions.reduce((totalPrice, item) => totalPrice + item.margin, 0)
      if(isNaN(this.PLList)){
        this.PLList = 0
      }
      this.NetUserValue = (this.balance+this.PLList-this.frozenCash).toFixed(2)
      // console.log(this.balance,this.PLList,this.frozenCash,this.occupyBond)
      // （用户余额+总浮动盈亏+占用保证金）÷占用保证金
      // console.log(this.PLList)
    if( this.occupyBond !== 0){
      // console.log((((this.balance+this.PLList-this.frozenCash)/this.occupyBond)*100))
        this.RiskRate = (((this.balance+this.PLList-this.frozenCash)/this.occupyBond)*100).toFixed(2)+'%'
      }else {
        this.RiskRate = 0
      }
    },
