        let beginTime = this.getDay(-5);//5天前日期
        let endTime = this.getDay(0);//当天日期
        this.$axios.get('http://'+this.discUrl+':7777/api/position/tradeHistory?userId='+this.uid+'&beginTime='+beginTime+'&endTime='+endTime).then(res =>{
