            for(let i=0,len = this.tradeRecordList.length;i<len;i++){
              let thisTowDate = new Date(this.tradeRecordList[i].closetime);
              let currentTowTime = thisTowDate.getFullYear() + '-' + (thisTowDate.getMonth() + 1).toString().padStart(2,'0') + '-' + (thisTowDate.getDate()).toString().padStart(2,'0') + ' ' + (thisTowDate.getHours()).toString().padStart(2,'0') + ':' + (thisTowDate.getMinutes()).toString().padStart(2,'0');
              this.tradeRecordList[i].closetime = currentTowTime
              let thisTowDate2 = new Date(this.tradeRecordList[i].opentime);
              let currentTowTime2 = thisTowDate2.getFullYear() + '-' + (thisTowDate2.getMonth() + 1).toString().padStart(2,'0') + '-' + (thisTowDate2.getDate()).toString().padStart(2,'0') + ' ' + (thisTowDate2.getHours()).toString().padStart(2,'0') + ':' + (thisTowDate2.getMinutes()).toString().padStart(2,'0');
              this.tradeRecordList[i].opentime = currentTowTime2
              if(this.tradeRecordList[i].direction === 0){
                this.tradeRecordList[i].direction = '买'
              }else if(this.tradeRecordList[i].direction === 1){
                this.tradeRecordList[i].direction = '卖'
              }
            }
          }

        })
      }
    },
    getDay(day){
    let today = new Date();
    let targetday_milliseconds=today.getTime() + 1000*60*60*24*day;
    today.setTime(targetday_milliseconds); //注意，这行是关键代码
    let tYear = today.getFullYear();
    let tMonth = today.getMonth();
    let tDate = today.getDate();
    tMonth = this.doHandleMonth(tMonth + 1);
    tDate = this.doHandleMonth(tDate);
    return tYear+"-"+tMonth+"-"+tDate;
  },
    doHandleMonth(month){
    let m = month;
    if(month.toString().length == 1){
      m = "0" + month;
    }
    return m;
  },

    // 发送委托单查询-计时器
    orderTimeData(){
      this.global.ws.onopen = this.setEntrust()
