    // 发送委托单查询
    setEntrust(){
      let actions;
      let that = this
      actions = {
        action: "loadorder",
        reqID: 50,
        data: {
          userId : that.uid,
        },
      }

      that.global.ws.send(JSON.stringify(actions))
