    // 接收委托单查询
    getEntrust(e){
      this.redata = JSON.parse(e.data)
      console.log( this.redata)
      let that = this
      let tickArr = JSON.parse(localStorage.getItem('tickCopy'))
      if(this.redata.action === "loadorder"){
        if(this.redata.data.code === 0){
          let dataList = this.redata.data.object
          console.log(dataList,'dataList')
          this.entrustList = []
          for(let i=0,len = dataList.length;i<len;i++){
            if(dataList[i].status === 0||dataList[i].status === 2||dataList[i].status === 4 && dataList[i].priceType === 0){
              let thisTowDate = new Date(dataList[i].createtime);
              let currentTowTime =(thisTowDate.getMonth() + 1).toString().padStart(2,'0') + '-' + (thisTowDate.getDate()).toString().padStart(2,'0') + ' ' + (thisTowDate.getHours()).toString().padStart(2,'0') + ':' + (thisTowDate.getMinutes()).toString().padStart(2,'0');
              dataList[i].createtime = currentTowTime
              if(dataList[i].direction === 0){
                dataList[i].direction = '买'
              }else if(dataList[i].direction === 1){
                dataList[i].direction = '卖'
              }
              this.entrustList.push(dataList[i])
            }
          }
          this.$axios.get('http://'+this.discUrl+':8189/instruments').then(res =>{
            let instrumentNameList = res.data.data
            for(let i=0,len = instrumentNameList.length;i<len;i++){
              for (let j=0,len = this.entrustList.length;j<len;j++){
                if(instrumentNameList[i].instrumentid === this.entrustList[j].instrumentid){
                  this.entrustList[j].name = instrumentNameList[i].nameCh
                  this.entrustList[j].digits = instrumentNameList[i].digits
                }
              }
            }
          })
          console.log( tickArr)
          // for(let i in tickArr) {
            for (let j=0,len = this.entrustList.length;j<len;j++){
              console.log()
              if(tickArr[this.entrustList[j].instrumentid] !== undefined){
                // console.log(i,this.entrustList[j].instrumentid,tickArr[i].bidprice)
                this.entrustList[j].bidPrice = tickArr[this.entrustList[j].instrumentid].bidprice
                this.entrustList[j].askPrice = tickArr[this.entrustList[j].instrumentid].askPrice
              }
            }
          // }
          console.log(this.entrustList)
        }
      }else if(this.redata.action === 'notifitrade' && this.redata.data != null ){
        this.orderTimeData()
      }
      // console.log(this.entrustList)
