    websocketonmessage2(e){
      this.redata = JSON.parse(e.data)
      let _this = this
      console.log(this.redata)
      if (this.redata.action === "discarded") {
        if(this.redata.data.code === 0){
          Toast({
            duration:1000,
            message:_this.$t('noNetwork'),
            position:'bottom',
            onClose() {
              _this.Logout()
            }
          })
        }
      }
      if(this.redata.action === "documentary" ){
        if(this.redata.data.object.code === 0){
          _this.$router.push({
            name:'blank'
          })
          Toast({
            duration:1500,
            position:'bottom',
            message:'跟单成功'
          })
        }else {
          _this.followState = false
          Toast({
            duration:2000,
            position:'bottom',
            message:_this.redata.data.object.msg+'，已取消跟单',
          })
        }
      }
      if(this.redata.action === "closeDocumentary" ){
        if(this.redata.data.code === 0) {
          Toast({
            duration: 2000,
            position: 'bottom',
            message: '跟单状态已结束',
          })
          _this.$router.push({
            name: 'blank'
          })
        }
      }
      if(this.redata.action === "mutipointlogin"){
        Toast({
          message:'您的账号已在别的设备登录，请确认是否本人操作',
          duration:2000,
          position:'bottom',
          onOpened() {
            localStorage.jix = 'jix'
            _this.Logout()
          }
        })
      }else {
        if(this.redata.data.code === 0 && this.redata.action === "heartbeat"){
          // console.log('连接正常')
        }
        if(this.redata.data.code === 0 && this.redata.action === "loadusermoney"){
          this.balance = this.redata.data.object.balance
          this.frozenCash = this.redata.data.object.frozenCash
        }

      }
      if(this.redata.action === 'notifitrade' && this.redata.data != null ){
        _this.$router.push({
          name:'blank'
        })
      }
    },
    // 接收初始持仓单列表
    websocketonmessage (e) { // 数据接收
      this.redata = JSON.parse(e.data)
      console.log(this.redata.data.object,'原数组')
      let _this = this
      if(this.redata.action === 'login' && this.redata.data.code === 0){
        localStorage.setItem('token',this.redata.data.object.token)
        this.handdleMsg()
        this.$router.push({
          name:'blank'
        })
      }
      // if(this.redata.data.code === 102  && this.redata.action === 'loadposition'){
      //   Toast({
      //     message:'登录状态已过期，请重新登录！',
      //     position:'bottom',
      //     duration:1500,
      //     onClose() {
      //       _this.Logout()
      //     }
      //   })
      // }
      if(this.redata.data.code === 0 && this.redata.data.object != null && this.redata.action === 'loadposition'){
        this.MergeState = false
        console.log(this.redata.data.object,'原数组')
        // this.holdList = this.redata.data.object
        this.holdPositions = this.redata.data.object
        for(let i=0,len = this.holdPositions.length;i<len;i++){
          if(this.holdPositions[i].direction === 0){
            this.holdPositions[i].direction = '买'
          }else if(this.holdPositions[i].direction === 1){
            this.holdPositions[i].direction = '卖'
          }
          let thisTowDate = new Date(this.holdPositions[i].opentime);
          let currentTowTime = thisTowDate.getFullYear() + '-' + (thisTowDate.getMonth() + 1).toString().padStart(2,'0') + '-' + (thisTowDate.getDate()).toString().padStart(2,'0') + ' ' + (thisTowDate.getHours()).toString().padStart(2,'0') + ':' + (thisTowDate.getMinutes()).toString().padStart(2,'0');
          this.holdPositions[i].opentime = currentTowTime
        }
        // let changeArr = this.mergeArr(this.holdPositions)
        let result = this.holdPositions.reduce((groups, item)=>{
          console.log((item.volume-item.volumeClosed-item.volumeFrozen),'item1')
          let groupFound= groups.find(arrItem => item.instrumentid === arrItem.instrumentid&&item.direction === arrItem.direction);
          item.openPrice = (item.openPrice*(item.volume-item.volumeClosed-item.volumeFrozen))
          if(groupFound) {
            groupFound.volume += item.volume-item.volumeClosed-item.volumeFrozen;
            groupFound.openPrice =Number(groupFound.openPrice) + Number(item.openPrice)
            groupFound.margin += item.margin;
            groupFound.commission += item.commission;
            if(groupFound.orderid.indexOf(item.orderid)  === -1) {  //去重
              groupFound.orderid.push(item.orderid);
            }
            if(groupFound.id.indexOf(item.id)  === -1) {  //去重
              groupFound.id.push(item.id);
            }
            if(groupFound.opentime.indexOf(item.opentime)  === -1) {  //去重
              groupFound.opentime.push(item.opentime);
            }
          } else {
            console.log(item,'item')
            //不要直接在原来的对象修改，新建对象
            if((item.volume-item.volumeClosed-item.volumeFrozen)>0){
              let newGroup = {
                instrumentid: item.instrumentid,
                direction: item.direction,
                winPrice: item.winPrice,
                losePrice: item.losePrice,
                volume: item.volume,
                volumeClosed: item.volumeClosed,
                volumeFrozen: item.volumeFrozen,
                orderid: [item.orderid],
                id: [item.id],
                opentime: [item.opentime],
                openPrice: item.openPrice,
                margin: item.margin,
                commission: item.commission,
              }
              groups.push(newGroup);
            }
          }
          return groups;
        },[]);
        console.log(result,'result')
        // this.MergeState = true
        this.getMultiplier(result)
      }
      // 获取余额
      this.global.ws.onopen = this.onopen2()
      this.global.ws.onmessage = function(res) {
        _this.websocketonmessage2 (res)
      };
    },
