          },
        })
        this.contentStyle = 'trade-show'
      }else {
        this.$router.push({
          name:'trade',
        })
        this.sheetOff = true
      }
    },
    // 一键平仓
    oneClosePosition (index) {
      let _this = this
      console.log(_this.holdPositions[index])
      if(_this.followState){
        Dialog.confirm({
          title: _this.$t('documentary'),
          className:'noFollow',
          confirmButtonColor:"#fff",
          cancelButtonColor:"#666666"
        }).then(() => {
          _this.$router.push({
            name:'quickClosing',
            params:{
              userid:_this.uid,
              digits:_this.holdPositions[index].digits,
              name:_this.holdPositions[index].name,
              nameEN:_this.holdPositions[index].nameEN,
              positionid:_this.holdPositions[index].id,
              priceperpoint:_this.holdPositions[index].priceperpoint,
              rate:_this.holdPositions[index].rate,
              MaxVolume:_this.holdPositions[index].volume,
              instrumentid:_this.holdPositions[index].instrumentid,
              average:_this.holdPositions[index].average,
              directionClose:_this.holdPositions[index].direction,
              margin:_this.holdPositions[index].margin,
              commission:_this.holdPositions[index].commission,
              groupid:_this.holdPositions[index].groupid,
              commissionClose:_this.holdPositions[index].commissionClose
            }
          })
        }).catch(() => {
        });
      }else {
        _this.$router.push({
          name:'quickClosing',
          params:{
            userid:_this.uid,
            digits:_this.holdPositions[index].digits,
            name:_this.holdPositions[index].name,
            nameEN:_this.holdPositions[index].nameEN,
            positionid:_this.holdPositions[index].id,
            priceperpoint:_this.holdPositions[index].priceperpoint,
            rate:_this.holdPositions[index].rate,
            MaxVolume:_this.holdPositions[index].volume,
            instrumentid:_this.holdPositions[index].instrumentid,
            average:_this.holdPositions[index].average,
            directionClose:_this.holdPositions[index].direction,
            margin:_this.holdPositions[index].margin,
            commission:_this.holdPositions[index].commission,
            groupid:_this.holdPositions[index].groupid,
            commissionClose:_this.holdPositions[index].commissionClose
          }
        })
      }
    },
    // 弹出框-平仓
    recordConfirm(){
      this.showDetails = false
    },
    tradeDetails (idx) {
      this.showDetails = true
      this.detDirection = this.tradeRecordList[idx].direction
      this.detInstrumentName = this.tradeRecordList[idx].instrumentName
      this.detInstrumentId = this.tradeRecordList[idx].instrumentId
      this.datOpenPrice = this.tradeRecordList[idx].openPrice
      this.datClosePrice = this.tradeRecordList[idx].closePrice
      this.datPL = this.tradeRecordList[idx].win
      this.datVolumeClosed = this.tradeRecordList[idx].volumeClosed
      this.datVolume = this.tradeRecordList[idx].volume
      // datSource1:'User orders',
      //   datSource2:'System liquidation',
      //   datSource3:'Stop profit stop loss close position',
      //   datSource4:'Administrator forced',
      //   datSource5:'Place an order',
      if(this.tradeRecordList[idx].source === 0){
        this.datSource = this.$t('datSource1')
      }else if(this.tradeRecordList[idx].source === 1){
        this.datSource = this.$t('datSource2')
      }else if(this.tradeRecordList[idx].source === 2){
        this.datSource = this.$t('datSource3')
      }else if(this.tradeRecordList[idx].source === 3){
        this.datSource = this.$t('datSource4')
      }else if(this.tradeRecordList[idx].source === 4){
        this.datSource = this.$t('datSource5')
      }
      this.datMargin = this.tradeRecordList[idx].margin
      this.datCommission = this.tradeRecordList[idx].commission
      this.datInterest = this.tradeRecordList[idx].interest
      this.datOpentime = this.tradeRecordList[idx].opentime
      this.datClosetime = this.tradeRecordList[idx].closetime
      this.datUserBalance = this.tradeRecordList[idx].userBalance
      this.datOrderId = this.tradeRecordList[idx].orderId
    },
    // 设置止盈止损-交易开关事件
    setPLSheet(idx){
      let that = this
      console.log(that.holdPositions[idx])
        that.$router.push({
          name:'PLSheet',
          params:{
            NEWPRICE:that.holdPositions[idx].NEWPRICE,
            positionid:that.holdPositions[idx].id,
            name:that.holdPositions[idx].name,
            nameEN:that.holdPositions[idx].nameEN,
            instrumentid:that.holdPositions[idx].instrumentid,
            digits:that.holdPositions[idx].digits,
            average:that.holdPositions[idx].average,
            volume:that.holdPositions[idx].volume,
            direction:that.holdPositions[idx].direction,
            rate:that.holdPositions[idx].rate,
            priceperpoint:that.holdPositions[idx].priceperpoint,
            tick:that.holdPositions[idx].tick,
            winPrice:that.holdPositions[idx].winPrice,
            losePrice:that.holdPositions[idx].losePrice,
          }
        })
    },
    // 获取合约乘数
    getMultiplier(result){
      console.log(result,'result')
      this.$axios.get('http://'+this.discUrl+':8189/instruments').then(res =>{
        console.log(res,'合约')
        let instrumentNameList = res.data.data
        console.log(instrumentNameList)
        for(let i=0,len = instrumentNameList.length;i<len;i++){
          for (let j=0,len = result.length;j<len;j++){
            if(instrumentNameList[i].instrumentid === result[j].instrumentid){
              result[j].priceperpoint = instrumentNameList[i].priceperpoint
              result[j].name = instrumentNameList[i].name
              result[j].nameEN = instrumentNameList[i].nameEN
              result[j].tick = instrumentNameList[i].tick
              result[j].currencyQuote = instrumentNameList[i].currencyQuote
              result[j].digits = instrumentNameList[i].digits
              result[j].groupid = instrumentNameList[i].groupid
              result[j].commissionClose = instrumentNameList[i].commissionClose
              result[j].tick = instrumentNameList[i].tick
              result[j].type = instrumentNameList[i].type
            }
          }
        }
        this.exchangeRate(result)
      })


    },
    // 获取汇率
    exchangeRate(result){
      let _this = this
      let tickCopy = JSON.parse(localStorage.getItem('tickCopy'))
      this.$axios.get('http://'+this.discUrl+':7777/api/exchangerate/all').then(res =>{
        console.log(res,'汇率')
        if(res.data.code === 0){
          _this.rateList = res.data.object
          for(let i=0,len = result.length;i<len;i++){
            console.log(result[i].openPrice)
            result[i].volume = result[i].volume-result[i].volumeClosed-result[i].volumeFrozen
            result[i].average = (result[i].openPrice/result[i].volume).toFixed(result[i].digits)
            result[i].showContent = false
            result[i].NEWPL = 0
            result[i].NEWCPL = 0
            for (let j=0,len = _this.rateList.length;j<len;j++){
              if(_this.rateList[j].currency  ===  result[i].currencyQuote) {
                result[i].rate = _this.rateList[j].rate
              }
            }
          }
          _this.holdPositions = _this.newPriceElements(tickCopy,result)
          this.PLList = this.holdPositions.reduce((totalPrice, item) =>totalPrice + item.NEWCPL , 0)
          this.MergeState = true
          console.log(result,11111)
          // return result
        }
      })
    },
    newPriceElements(tickCopy,arr){
      console.log(arr)
      let that = this
      if(tickCopy != null){
        for(let i=0,len = arr.length;i<len;i++){
          if(tickCopy[arr[i].instrumentid] != null && arr[i] != null){
            // console.log(tickCopy[arr[i].instrumentid],'tickCopy')
            // console.log(arr[i],'arr[i]')
            arr[i].NEWPRICE = (tickCopy[arr[i].instrumentid].price).toFixed(arr[i].digits)
            arr[i].diffPrice = tickCopy[arr[i].instrumentid].price-tickCopy[arr[i].instrumentid].preclose
            console.log(arr[i],'priceperpoint')
            if(arr[i].direction === '卖'){
              if(arr[i].type === 4){
                arr[i].NEWPL = Number((Number(tickCopy[arr[i].instrumentid].bidprice-arr[i].average)*arr[i].volume*arr[i].priceperpoint).toFixed(arr[i].digits))*-1;
                arr[i].NEWCPL = Number((Number(tickCopy[arr[i].instrumentid].bidprice-arr[i].average)*arr[i].volume*arr[i].priceperpoint*arr[i].rate).toFixed(arr[i].digits))*-1;
              }else {
                arr[i].NEWPL = Number((Number(tickCopy[arr[i].instrumentid].price-arr[i].average)*arr[i].volume*arr[i].priceperpoint).toFixed(arr[i].digits))*-1;
                arr[i].NEWCPL = Number((Number(tickCopy[arr[i].instrumentid].price-arr[i].average)*arr[i].volume*arr[i].priceperpoint*arr[i].rate).toFixed(arr[i].digits))*-1;
              }

            }else {
              if(arr[i].type === 4){
                arr[i].NEWPL = Number((Number(tickCopy[arr[i].instrumentid].askprice-arr[i].average)*arr[i].volume*arr[i].priceperpoint).toFixed(arr[i].digits));
                arr[i].NEWCPL = Number((Number(tickCopy[arr[i].instrumentid].askprice-arr[i].average)*arr[i].volume*arr[i].priceperpoint*arr[i].rate).toFixed(arr[i].digits));
              }else {
                arr[i].NEWPL = Number((Number(tickCopy[arr[i].instrumentid].price-arr[i].average)*arr[i].volume*arr[i].priceperpoint).toFixed(arr[i].digits));
                arr[i].NEWCPL = Number((Number(tickCopy[arr[i].instrumentid].price-arr[i].average)*arr[i].volume*arr[i].priceperpoint*arr[i].rate).toFixed(arr[i].digits));
              }
            }
          }else {
            arr[i].newPrice = 0
          }
        }
      }
      return arr
    },
    // 快捷反手
    quickBackhand(index){
      let _this = this
      console.log(this.holdPositions[index])
      if(_this.followState){
        Dialog.confirm({
          title: _this.$t('documentary'),
          className:'noFollow',
          confirmButtonColor:"#fff",
          cancelButtonColor:"#666666"
        }).then(() => {
          _this.$router.push({
            name:'quickBackhand',
            params:{
              userid:_this.uid,
              name:_this.holdPositions[index].name,
              nameEN:_this.holdPositions[index].nameEN,
              digits:_this.holdPositions[index].digits,
              positionid:_this.holdPositions[index].id,
              MaxVolume:_this.holdPositions[index].volume,
              instrumentid:_this.holdPositions[index].instrumentid,
              average:_this.holdPositions[index].average,
              directionClose:_this.holdPositions[index].direction,
              margin:_this.holdPositions[index].margin,
              commission:_this.holdPositions[index].commission
            }
          })
        }).catch(() => {
        });
      }else {
        _this.$router.push({
          name:'quickBackhand',
          params:{
            userid:_this.uid,
            name:_this.holdPositions[index].name,
            nameEN:_this.holdPositions[index].nameEN,
            digits:_this.holdPositions[index].digits,
            positionid:_this.holdPositions[index].id,
            MaxVolume:_this.holdPositions[index].volume,
            instrumentid:_this.holdPositions[index].instrumentid,
            average:_this.holdPositions[index].average,
            directionClose:_this.holdPositions[index].direction,
            margin:_this.holdPositions[index].margin,
            commission:_this.holdPositions[index].commission
          }
        })
      }
    },
    // 追加下单
    addOrder(idx){
      let that = this
      console.log(that.holdPositions[idx])
      if(that.followState){
        Dialog.confirm({
          title: that.$t('documentary'),
          className:'noFollow',
          confirmButtonColor:"#fff",
          cancelButtonColor:"#666666"
        }).then(() => {
          that.$router.push({
            name:'addOrder',
            params:{
              NEWPRICE:that.holdPositions[idx].NEWPRICE,
              positionid:that.holdPositions[idx].id,
              name:that.holdPositions[idx].name,
              nameEN:that.holdPositions[idx].nameEN,
              instrumentid:that.holdPositions[idx].instrumentid,
              digits:that.holdPositions[idx].digits,
              average:that.holdPositions[idx].average,
              volume:that.holdPositions[idx].volume,
              direction:that.holdPositions[idx].direction,
              rate:that.holdPositions[idx].rate,
              priceperpoint:that.holdPositions[idx].priceperpoint,
              tick:that.holdPositions[idx].tick,
              bondClose:that.holdPositions[idx].margin/that.holdPositions[idx].volume,
              serviceChargeClose:that.holdPositions[idx].commission/that.holdPositions[idx].volume,
            }
          })
        }).catch(() => {
        });
      }else {
        that.$router.push({
          name:'addOrder',
          params:{
            NEWPRICE:that.holdPositions[idx].NEWPRICE,
            positionid:that.holdPositions[idx].id,
            name:that.holdPositions[idx].name,
            nameEN:that.holdPositions[idx].nameEN,
            instrumentid:that.holdPositions[idx].instrumentid,
            digits:that.holdPositions[idx].digits,
            average:that.holdPositions[idx].average,
            volume:that.holdPositions[idx].volume,
            direction:that.holdPositions[idx].direction,
            rate:that.holdPositions[idx].rate,
            priceperpoint:that.holdPositions[idx].priceperpoint,
            tick:that.holdPositions[idx].tick,
            bondClose:that.holdPositions[idx].margin/that.holdPositions[idx].volume,
            serviceChargeClose:that.holdPositions[idx].commission/that.holdPositions[idx].volume,
          }
        })
      }
    }
  }
}
</script>

