<template>
  <div>
    <div class="actionSheet">
      <van-action-sheet v-model="sheetTradeShow" round overlay  class="sheet-box"  @cancel="noShowSheet" @click-overlay="noShowSheet">
        <div class="sheet-content">
          <div class="title">
            <div class="name">
              <div :class="$i18n.locale === 'en'?'name-en':''">{{$i18n.locale === 'en'?nameEN:name}}</div>
              <div style="color: #808080">{{instrumentid}}</div>
            </div>
            <div class="close">
              <div :style="{color:add<0?$t('fall'):$t('rise')}">{{newClose}}</div>
              <div :style="{color:add<0?$t('fall'):$t('rise')}"><span v-if="add > 0">+</span>{{add}} {{diffPriceRate}}</div>
            </div>
            <div class="BAPrice">
              <div>{{$t('Buy')}}：<span style="color:#FD4F50">{{bidPrice}}</span></div>
              <div>{{$t('Sell')}}：<span style="color: #0CAD6B">{{askPrice}}</span></div>
            </div>
          </div>
          <div class="title">
            <div class="name1">
              <div>{{$t('AveragePrice')}}</div>
              <div >{{average}}</div>
            </div>
            <div class="name1">
              <div>{{$t('Number')}}</div>
              <div>{{stepperMax}}</div>
            </div>
            <div class="name1">
              <div>{{$t('PL')}}</div>
              <div :style="{color:NEWCPL<0?$t('fall'):$t('rise')}">${{NEWCPL}}</div>
            </div>
          </div>
          <div class="number number-but" style="color: #222">
            <div class="number-title">{{$t('quickClosing')}}</div>
            <van-button :disabled="stepperMax>=1?false:true" @click="quickOrder(1)">1</van-button>
            <van-button :disabled="stepperMax>=2?false:true" @click="quickOrder(2)">2</van-button>
            <van-button :disabled="stepperMax>=3?false:true"  @click="quickOrder(3)">3</van-button>
            <van-button :disabled="stepperMax>=4?false:true"  @click="quickOrder(4)">4</van-button>
            <van-button :disabled="stepperMax>=5?false:true"  @click="quickOrder(5)">5</van-button>
            <van-button class="all-but" @click="quickOrder(Max)">{{$t('whole')}}</van-button>
<!--            <van-stepper   v-model="stepper" :max="stepperMax"/>-->
          </div>
          <div class="number" >
            <div style="color: #222;" class="number-title1" :style="{'margin-right':$i18n.locale === 'en'?'0':''}">{{$t('Number')}}</div>
            <van-stepper  v-model="stepper"  :max="stepperMax"/>
            <!--          <div class="sheet-trade-but" style="border-color: #FF7100;background-color: #FF7100;color: white" @click="handdleMsg">设置</div>-->
          </div>
          <div class="sheet-but">
<!--            <div style="background-color:#2E8CFF;" @click="sellOut">追加下单</div>-->
            <div @click="closePosition">{{$t('CloseP')}}</div>
          </div>
        </div>
      </van-action-sheet>
    </div>
    <div class="dialog">
     <div class="sellOut">
      <van-dialog
        closeOnPopstate
        v-model="showClosePosition"
        show-cancel-button
        :cancel-button-text="$t('cancel')"
        cancelButtonColor="#8C8B8B" confirmButtonColor="#1988FF"
        show-confirm-button
        :confirm-button-text="$t('ConfirmC')"
        @confirm="ConfirmClosing"
      >
        <div slot="title" class="dia-title">{{$t('OrderInform')}}</div>
        <div slot="default" class="dia-content">
          <div>
            <div>{{$t('Name')}}：</div>
            <div class="dia-content-title">{{$i18n.locale === 'en'?nameEN:name}}</div>
          </div>
          <div>
            <div>{{$t('code')}}：</div>
            <div class="dia-content-title1">{{instrumentid}}</div>
          </div>
          <div>
            <div>{{$t('direction')}}：</div>
            <div><span :style="{color:directionClose === '买'? '#FD4F50':'#0CAD6B'}">{{directionClose === '买'?$t('buy'):$t('sell')}}</span></div>
          </div>
          <div>
            <div>{{$t('PL')}}($)：</div>
            <div :style="{color:NEWCPL<0?'#0CAD6B':'#FD4F50'}"> {{NEWCPL}}</div>
          </div>
          <div>
            <div>{{$t('LatestPrice')}}：</div>
            <div :style="{color:add<0?'#0CAD6B':'#FD4F50'}">{{newClose}}</div>
          </div>
          <div>
            <div>{{$t('AveragePrice')}}：</div>
            <div>{{average}}</div>
          </div>
          <div>
            <div>{{$t('Qty')}}：</div>
            <div>{{stepper}}</div>
          </div>
          <div>
            <div>{{$t('margin')}}：</div>
            <div>{{bondClose}}</div>
          </div>
          <div>
            <div>{{$t('ServiceCharge')}}：</div>
            <div>{{serviceChargeClose}}</div>
          </div>
        </div>
      </van-dialog>
    </div>
    </div>
  </div>
</template>

<script>
  import EP from '../../../utils/encryption'
  import { Toast } from 'vant';
    export default {
      name: "closeSheet",
      data(){
          return{
            discUrl:'',
            sheetTradeShow:'',
            showSellOut:false,
            upTitle:'11',
            stepperMax:null,
            stepper:1,
            // 传给父组件的值
            stopProfit:'11',
            stopLoss:'1000',
            styleClass: 'start',
            show:false,
            instrumentid:'',
            newClose:0,
            // 买价
            bidPrice:0 ,
            // 卖价
            askPrice:0,
            // 用户id
            uid:'',
            // bidPriceVal:this.bidPrice
            // 弹出框-平仓显示
            showClosePosition: false,
            // 平仓
            average:0,
            bondClose:0,
            directionClose:0,
            serviceChargeClose:0,
            changeContentOff:true,
            // 详细订单
            detailedData:[],
            // 存放订单号
            orderList:[],
            tsNum:0,

            name:'',
            nameEN:'',
            add:0,
            diffPriceRate:0,
            NEWCPL:0,
            Max:0,
            tick:0,
            digits:0,
            tickCopyData:''
          }
      },
      props:{
        appTick:{
          type:Array,
        },
        tradeTab:{
          type:Number
        },
        changeShow:{
          type:Object
        }
      },
      watch:{
        appTick(newVal) {
          // console.log(newVal,'newVal')
          if(JSON.parse(localStorage.tickCopy) !== ''){
            this.tickCopyData = JSON.parse(localStorage.tickCopy)
          }
          if (this.tickCopyData[this.instrumentid] !== undefined) {
            let data = this.tickCopyData[this.instrumentid]
            this.newClose = (data.price).toFixed(this.digits)
            this.bidPrice = (data.bidprice).toFixed(this.digits)
            this.askPrice = (data.askprice).toFixed(this.digits)
            this.add = (data.price-data.preclose).toFixed(this.digits)
            if(data.preclose === 0){
              this.diffPriceRate = '--'
            }else {
              this.diffPriceRate = (((Number(data.price)-Number(data.preclose))/data.preclose)*100).toFixed(this.digits)+'%';
            }
            if(this.directionClose === '卖'){
              this.NEWCPL = Number((Number(data.price-this.average)*this.stepperMax*this.priceperpoint*this.rate).toFixed(2))*-1;
            }else {
              this.NEWCPL = Number((Number(data.price-this.average)*this.stepperMax*this.priceperpoint*this.rate).toFixed(2));
            }
          }
        },
        // changeShow(newVal){
        //   this.instrumentid =newVal.instrumentid
        //   console.log(this.instrumentid,'54545')
        //   this.upTitle =newVal.upTitle
        //   this.stepperMax =newVal.MaxVolume
        //   this.directionClose =newVal.directionClose
        //   this.positionid =newVal.positionid
        //   this.commission =newVal.commission
        //   this.margin =newVal.margin
        //   this.averageClose =newVal.average
        //   this.uid =newVal.userid
        //   this.handdleMsg(this.instrumentid)
        // }
      },
      created(){
        // this.discUrl = '121.43.137.150'
        // this.getContract()
        // this.handdleMsg()
        console.log(this.upTitle)
        let routeData = this.$route.params
        this.instrumentid = routeData.instrumentid
        this.name = routeData.name
        this.nameEN = routeData.nameEN
        this.digits = routeData.digits
        this.priceperpoint = routeData.priceperpoint
        this.rate = routeData.rate
        this.directionClose = routeData.directionClose
        this.positionid = routeData.positionid
        this.margin = routeData.margin
        this.commission = routeData.commission
        this.tick = routeData.tick
        this.digits = routeData.digits
        this.uid = this.$route.params.userid
        this.average = this.$route.params.average
        this.groupid = this.$route.params.groupid
        this.stepperMax = routeData.MaxVolume
        this.Max = routeData.MaxVolume
        let tickCopy = JSON.parse(localStorage.getItem('tickCopy'))
        let tickCopyData = tickCopy[this.instrumentid]
        console.log(tickCopy[this.instrumentid])
        this.newClose = (tickCopyData.price).toFixed(this.digits)
        this.bidPrice = (tickCopyData.bidprice).toFixed(this.digits)
        this.askPrice = (tickCopyData.askprice).toFixed(this.digits)
        this.add = (tickCopyData.price-tickCopyData.preclose).toFixed(this.digits)
        if(tickCopyData.preclose === 0){
          this.diffPriceRate = '--'
        }else {
          this.diffPriceRate = (((Number(tickCopyData.price)-Number(tickCopyData.preclose))/tickCopyData.preclose)*100).toFixed(this.digits)+'%';
        }
        console.log(tickCopyData.price,this.average,this.stepperMax,this.priceperpoint,'this.directionClose')
        if(this.directionClose === '卖'){
          this.NEWCPL = (((tickCopyData.price-Number(this.average))*this.stepperMax*this.priceperpoint*this.rate).toFixed(2))*-1;
        }else {
          this.NEWCPL = (((tickCopyData.price-Number(this.average))*this.stepperMax*this.priceperpoint*this.rate).toFixed(2));
        }
        this.handdleMsg()
      },
      methods:{
        // 弹出框-卖出
        // 弹出框-平仓
        closePosition () {
          this.showClosePosition = true
          this.bondClose = (this.margin/this.stepperMax)*this.stepper
          console.log(this.$route.params.commissionClose)
          let dis = EP.decrypt(localStorage.getItem('discount'),'')
          let discount = JSON.parse(dis)
          if(JSON.stringify(discount.data) !== '{}'){
            for(let i in discount.data) {
              if(i === this.groupid){
                console.log(discount.data[i])
                this.serviceChargeClose = (((this.$route.params.commissionClose)*discount.data[i])/100)*this.stepper
              }
            }
          }else {
            console.log(111188888)
            this.serviceChargeClose = (this.$route.params.commissionClose)*this.stepper
          }
        },
        quickOrder(a){
          console.log(a)
          this.stepper = a
          this.showClosePosition = true
          this.bondClose = (this.margin/this.stepperMax)*this.stepper
          let dis = EP.decrypt(localStorage.getItem('discount'),'')
          let discount = JSON.parse(dis)
          console.log(discount.data)
          if(JSON.stringify(discount.data) !== '{}'){
            for(let i in discount.data) {
              if(i === this.groupid){
                console.log(discount.data[i])
                this.serviceChargeClose = (((this.$route.params.commissionClose)*discount.data[i])/100)*this.stepper
              }
            }
          }else {
            this.serviceChargeClose = (this.$route.params.commissionClose)*this.stepper
          }
        },
        // 关闭上拉框
        noShowSheet () {
          this.$emit('contentStyle',this.styleClass,this.show);
          localStorage.removeItem('positionid')
          localStorage.removeItem('upTitle')
          this.$router.push({
            name:'trade',
          })
        },
        // 获取初始持仓单
        handdleMsg(msg) {
          let that = this;
          if (that.global.ws && that.global.ws.readyState === 1) {
            that.global.ws.onopen = that.onopen()
          }
          that.global.ws.onmessage = function(res) {
            that.websocketonmessage (res)
          };
        },
        // 确认平仓
        ConfirmClosing(){
          let _this = this
          this.global.ws.onopen = this.onopen3()
          this.global.ws.onmessage = function(res) {
            _this.websocketonmessage3 (res)
          };
        },
        // 提交平仓
        onopen3 () { // websocket连接后发送数据(send发送)
          this.setOrderList(this.stepper)
          let balance,direction,actions;
          let that = this
          let diff = that.stepper
          let routeData = this.$route.params
          if(this.directionClose === '买'){
            direction = 0
          }else if(this.directionClose === '卖'){
            direction = 1
          }
          console.log(this.orderList)
          if(this.orderList.length<=1){
            balance = {
              action: "placeorder",
              reqID: 34,
              data: {
                id: null,
                userid: that.uid,
                settlementid: null,
                source: null,
                type: 1,
                positionid: that.positionid[0],
                instrumentid: that.instrumentid,
                orderRef: "9f538075-fff6-4e84-9fee-505ce91fecf6",
                ordersysid: null,
                direction: direction,
                volume: that.stepper,
                volumeTraded: null,
                priceType: 1,
                limitPrice: 0,
                distanceStopPrice: null,
                distanceProfitPrice: null,
                status: null,
                closed: null,
                frozenMargin: null,
                frozenMarginMember: null,
                tradingDay: null,
                createtime: null,
                updatetime: null,
                canceltime: null,
                hedge: null,
                hedgeChannel: null,
                distanceStop: 0.0,
                distanceProfit: 0.0
              }
            }
            console.log(balance,'balance')
            that.global.ws.send(JSON.stringify(balance))
          }else {
            let sum = 0
            for (let i=0,len = this.orderList.length;i<len;i++){
              balance = {
                action: "placeorder",
                reqID: 34,
                data: {
                  id: null,
                  userid: that.uid,
                  settlementid: null,
                  source: null,
                  type: 1,
                  positionid: that.orderList[i].id,
                  instrumentid: that.instrumentid,
                  orderRef: "9f538075-fff6-4e84-9fee-505ce91fecf6",
                  ordersysid: null,
                  direction: direction,
                  volume: that.orderList[i].volume,
                  volumeTraded: null,
                  priceType: 1,
                  limitPrice: 0,
                  distanceStopPrice: null,
                  distanceProfitPrice: null,
                  status: null,
                  closed: null,
                  frozenMargin: null,
                  frozenMarginMember: null,
                  tradingDay: null,
                  createtime: null,
                  updatetime: null,
                  canceltime: null,
                  hedge: null,
                  hedgeChannel: null,
                  distanceStop: 0.0,
                  distanceProfit: 0.0
                }
              }
              if(i<this.orderList.length-1){
                sum = sum + this.orderList[i].volume
              }
              if(i === this.orderList.length-1){
                balance = {
                  action: "placeorder",
                  reqID: 34,
                  data: {
                    id: null,
                    userid: this.uid,
                    settlementid: null,
                    source: null,
                    type: 1,
                    positionid: that.orderList[i].id,
                    instrumentid: this.instrumentid,
                    orderRef: "9f538075-fff6-4e84-9fee-505ce91fecf6",
                    ordersysid: null,
                    direction: direction,
                    volume: diff-sum,
                    volumeTraded: null,
                    priceType: 1,
                    limitPrice: 0,
                    distanceStopPrice: null,
                    distanceProfitPrice: null,
                    status: null,
                    closed: null,
                    frozenMargin: null,
                    frozenMarginMember: null,
                    tradingDay: null,
                    createtime: null,
                    updatetime: null,
                    canceltime: null,
                    hedge: null,
                    hedgeChannel: null,
                    distanceStop: 0.0,
                    distanceProfit: 0.0
                  }
                }
              }
              that.global.ws.send(JSON.stringify(balance))
            }
          }
          Toast.loading({
            message: that.$t('quickC'),
            duration: 0,
            mask:true,
            forbidClick: true,
            onClose() {
              that.$router.push({
                name:'blank'
              })
              Toast({
                message: that.$t('quickCS'),
                position:'bottom',
                duration: 2000,
              })
            }
          });
        },
        // 接收平仓回传
        websocketonmessage3(e){
          let that = this
          this.redata = JSON.parse(e.data)
          console.log(this.redata,'1111')
          if(this.redata.action === 'placeorder'){
            if(this.redata.data.code === 0){

            }else {
              Toast.fail({
                message: that.redata.data.msg,
                duration: 1000,
                onClose() {
                  that.$router.push({
                    name:'blank'
                  })
                }
              });
            }
          }
          if(this.redata.action === 'notifitrade' ||this.redata.action === 'notifiorder'){
            console.log(this.redata)
            this.tsNum++
            console.log(that.tsNum)
              if(that.tsNum >= that.orderList.length){
                console.log('平仓完成')
                Toast.clear();
              }
          }
        },
        // 获取详细订单
        onopen () { // websocket连接后发送数据(send发送)
          let actions;
          let that = this
          actions = {
            action: "loadposition",
            reqID: 45,
            data: {
              userId : that.uid,
            },
          }
          that.global.ws.send(JSON.stringify(actions))
        },
        // 接收详细订单
        websocketonmessage (e) { // 数据接收
          this.redata = JSON.parse(e.data)
          console.log(this.redata)
          if(this.redata.action === "notifitrade" && this.redata.data != null){
            this.$router.push({
              name:'blank'
            })
          }
          if(this.redata.action === "loadposition" && this.redata.data.code === 0 && this.redata.data.object != null){
            for(let i=0,len = this.redata.data.object.length;i<len;i++){
              if(this.instrumentid === this.redata.data.object[i].instrumentid){
                console.log(this.redata.data.object[i])
                this.redata.data.object[i].volume = this.redata.data.object[i].volume - this.redata.data.object[i].volumeClosed -this.redata.data.object[i].volumeFrozen
                this.detailedData.push(this.redata.data.object[i])
              }
            }
          }

        },
        setOrderList(a){
          let sarr = a
          let idx = 0
          let arr = this.detailedData
          console.log(arr,'a')
          console.log(this.detailedData)
          this.factorial(arr,sarr,idx)
        },
        factorial(arr,sarr,idx){
          this.orderList.push(arr[idx])
          if(arr[idx].volume >= sarr){
            return arr[idx].volume
          }else {
            return this.factorial(arr,sarr-arr[idx].volume,idx+1)
          }
        },
      }
    }
</script>

<style scoped lang="scss">
  @import "../../../style/trade";
</style>



